html {
  --active-color: #00acdc;
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header {
  background-color: #fff;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  display: block;
}

.nav {
  margin-left: auto;
}

.nav__links {
  align-items: center;
  margin: 0;
  padding: 0;
  font-size: 12px;
  list-style-type: none;
  display: flex;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: var(--active-color);
}

.nav__link + .nav__link {
  margin-left: 20px;
}

.is-active {
  color: var(--active-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

